import React, { useState } from 'react';
import { useQuery, useMutation, useQueryClient } from 'react-query';
import { 
  Clock, 
  Camera, 
  MapPin, 
  Calendar, 
  Plus, 
  Edit, 
  Trash2,
  CheckCircle,
  XCircle,
  AlertCircle,
  Download,
  Filter
} from 'lucide-react';
import { useAuth } from '@/contexts/AuthContext';
import { attendanceAPI } from '@/lib/api';
import Button from '@/components/ui/Button';
import Input from '@/components/ui/Input';
import Select from '@/components/ui/Select';
import Modal from '@/components/ui/Modal';
import Table from '@/components/ui/Table';
import LoadingSpinner from '@/components/LoadingSpinner';
import { cn, formatTime, formatDate, getStatusColor, formatWorkingHours } from '@/utils';
import Checkbox from '@/components/ui/Checkbox';

const Attendance: React.FC = () => {
  const { user } = useAuth();
  const queryClient = useQueryClient();
  const [isManualEntryModalOpen, setIsManualEntryModalOpen] = useState(false);
  const [isEditModalOpen, setIsEditModalOpen] = useState(false);
  const [selectedAttendance, setSelectedAttendance] = useState<any>(null);
  const [filters, setFilters] = useState({
    date: '',
    status: '',
    departments: [] as string[],
    page: 1,
  });

  const isAdmin = user?.role === 'admin';

  // Fetch attendance status
  const { data: attendanceStatus, isLoading: isStatusLoading } = useQuery(
    ['attendance-status'],
    attendanceAPI.getStatus,
    {
      refetchInterval: 60000, // Refetch every minute
    }
  );

  // Fetch attendance history
  const { data: attendanceHistory, isLoading: isHistoryLoading } = useQuery(
    ['attendance-history', filters],
    () => attendanceAPI.getHistory(filters),
    {
      keepPreviousData: true,
    }
  );

  // Fetch all attendances (admin only)
  const { data: allAttendances, isLoading: isAllLoading } = useQuery(
    ['all-attendances', filters],
    () => attendanceAPI.getAll(filters),
    {
      enabled: isAdmin,
      keepPreviousData: true,
    }
  );

  // Clock in mutation
  const clockInMutation = useMutation(
    (data: any) => attendanceAPI.clockIn(data),
    {
      onSuccess: () => {
        queryClient.invalidateQueries(['attendance-status']);
        queryClient.invalidateQueries(['attendance-history']);
        if (isAdmin) {
          queryClient.invalidateQueries(['all-attendances']);
        }
      },
      onError: (error: any) => {
        console.error('Clock in failed:', error);
      },
    }
  );

  // Clock out mutation
  const clockOutMutation = useMutation(
    (data: any) => attendanceAPI.clockOut(data),
    {
      onSuccess: () => {
        queryClient.invalidateQueries(['attendance-status']);
        queryClient.invalidateQueries(['attendance-history']);
        if (isAdmin) {
          queryClient.invalidateQueries(['all-attendances']);
        }
      },
      onError: (error: any) => {
        console.error('Clock out failed:', error);
      },
    }
  );

  // Manual entry mutation
  const manualEntryMutation = useMutation(
    (data: any) => attendanceAPI.createManual(data),
    {
      onSuccess: () => {
        queryClient.invalidateQueries(['attendance-history']);
        if (isAdmin) {
          queryClient.invalidateQueries(['all-attendances']);
        }
        setIsManualEntryModalOpen(false);
      },
      onError: (error: any) => {
        console.error('Manual entry failed:', error);
      },
    }
  );

  // Update attendance mutation
  const updateAttendanceMutation = useMutation(
    (data: any) => attendanceAPI.update(selectedAttendance?.id || '', data),
    {
      onSuccess: () => {
        queryClient.invalidateQueries(['attendance-history']);
        if (isAdmin) {
          queryClient.invalidateQueries(['all-attendances']);
        }
        setIsEditModalOpen(false);
        setSelectedAttendance(null);
      },
      onError: (error: any) => {
        console.error('Update failed:', error);
      },
    }
  );

  // Delete attendance mutation
  const deleteAttendanceMutation = useMutation(
    (id: string) => attendanceAPI.delete(id),
    {
      onSuccess: () => {
        queryClient.invalidateQueries(['attendance-history']);
        if (isAdmin) {
          queryClient.invalidateQueries(['all-attendances']);
        }
      },
      onError: (error: any) => {
        console.error('Delete failed:', error);
      },
    }
  );

  const handleClockIn = () => {
    // In a real app, this would capture photo and location
    const mockData = {
      photoUrl: 'https://example.com/photo.jpg',
      latitude: 37.7749,
      longitude: -122.4194,
      notes: '',
    };
    clockInMutation.mutate(mockData);
  };

  const handleClockOut = () => {
    // In a real app, this would capture photo and location
    const mockData = {
      photoUrl: 'https://example.com/photo.jpg',
      latitude: 37.7749,
      longitude: -122.4194,
      notes: '',
    };
    clockOutMutation.mutate(mockData);
  };

  const handleEdit = (attendance: any) => {
    setSelectedAttendance(attendance);
    setIsEditModalOpen(true);
  };

  const handleDelete = (id: string) => {
    if (window.confirm('Are you sure you want to delete this attendance record?')) {
      deleteAttendanceMutation.mutate(id);
    }
  };

  const handlePageChange = (page: number) => {
    setFilters(prev => ({ ...prev, page }));
  };

  const handleFilterChange = (key: string, value: string) => {
    setFilters(prev => ({ ...prev, [key]: value, page: 1 }));
  };

  const currentData = isAdmin ? allAttendances?.data : attendanceHistory?.data;
  const isLoading = isAdmin ? isAllLoading : isHistoryLoading;

  // Table columns
  const columns = [
    {
      key: 'date',
      label: 'Date',
      sortable: true,
      render: (value: string) => formatDate(value),
    },
    {
      key: 'user',
      label: 'Employee',
      render: (value: any, row: any) => (
        <div className="flex items-center gap-2">
          <div className="w-8 h-8 rounded-full bg-primary-100 dark:bg-primary-900 flex items-center justify-center">
            <span className="text-sm font-medium text-primary-700 dark:text-primary-300">
              {value?.name?.charAt(0).toUpperCase()}
            </span>
          </div>
          <span className="font-medium">{value?.name}</span>
        </div>
      ),
    },
    {
      key: 'clockInTime',
      label: 'Clock In',
      render: (value: string) => value ? formatTime(value) : '-',
    },
    {
      key: 'clockOutTime',
      label: 'Clock Out',
      render: (value: string) => value ? formatTime(value) : '-',
    },
    {
      key: 'status',
      label: 'Status',
      render: (value: string) => (
        <span className={cn('badge', `badge-${getStatusColor(value)}`)}>
          {value}
        </span>
      ),
    },
    {
      key: 'workingHours',
      label: 'Hours',
      render: (value: any) => {
        if (value && typeof value === 'number' && !isNaN(value)) {
          return formatWorkingHours(value);
        }
        return '-';
      },
    },
    {
      key: 'locationStatus',
      label: 'Location',
      render: (value: string) => (
        <span className={cn(
          'badge',
          value === 'valid' ? 'badge-success' :
          value === 'remote' ? 'badge-warning' : 'badge-danger'
        )}>
          {value === 'valid' ? 'Office' : value === 'remote' ? 'Remote' : 'Invalid'}
        </span>
      ),
    },
    {
      key: 'actions',
      label: 'Actions',
      width: '120px',
      render: (value: any, row: any) => (
        <div className="flex items-center gap-2">
          <button
            onClick={() => handleEdit(row)}
            className="p-1 text-secondary-500 hover:text-primary-600 transition-colors"
          >
            <Edit className="w-4 h-4" />
          </button>
          {isAdmin && (
            <button
              onClick={() => handleDelete(row.id)}
              className="p-1 text-secondary-500 hover:text-danger-600 transition-colors"
            >
              <Trash2 className="w-4 h-4" />
            </button>
          )}
        </div>
      ),
    },
  ];

  return (
    <div className="space-y-6">
      {/* Page header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-secondary-900 dark:text-white">
            Attendance
          </h1>
          <p className="text-secondary-600 dark:text-secondary-400">
            Manage your attendance records
          </p>
        </div>
        <div className="flex items-center gap-3">
          {!isAdmin && (
            <>
              {attendanceStatus?.data?.status === 'not_started' || 
               attendanceStatus?.data?.status === 'clocked_out' ? (
                <Button
                  variant="primary"
                  onClick={handleClockIn}
                  loading={clockInMutation.isLoading}
                >
                  <Camera className="w-4 h-4 mr-2" />
                  Clock In
                </Button>
              ) : (
                <Button
                  variant="secondary"
                  onClick={handleClockOut}
                  loading={clockOutMutation.isLoading}
                >
                  <Clock className="w-4 h-4 mr-2" />
                  Clock Out
                </Button>
              )}
            </>
          )}
          <Button
            variant="outline"
            onClick={() => setIsManualEntryModalOpen(true)}
          >
            <Plus className="w-4 h-4 mr-2" />
            Manual Entry
          </Button>
        </div>
      </div>

      {/* Current status (for employees) */}
      {!isAdmin && attendanceStatus?.data && (
        <div className="card">
          <h3 className="text-lg font-semibold text-secondary-900 dark:text-white mb-4">
            Current Status
          </h3>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div className="text-center p-4 rounded-lg bg-secondary-50 dark:bg-secondary-700">
              <Clock className="w-8 h-8 text-primary-500 mx-auto mb-2" />
              <p className="font-medium text-secondary-900 dark:text-white">
                {attendanceStatus.data.status === 'clocked_in' ? 'Clocked In' :
                 attendanceStatus.data.status === 'clocked_out' ? 'Clocked Out' : 'Not Started'}
              </p>
            </div>
            <div className="text-center p-4 rounded-lg bg-secondary-50 dark:bg-secondary-700">
              <Calendar className="w-8 h-8 text-success-500 mx-auto mb-2" />
              <p className="font-medium text-secondary-900 dark:text-white">
                {attendanceStatus.data.workingHours && typeof attendanceStatus.data.workingHours === 'number' && !isNaN(attendanceStatus.data.workingHours) ? 
                  formatWorkingHours(attendanceStatus.data.workingHours) : '0h'}
              </p>
              <p className="text-sm text-secondary-600 dark:text-secondary-400">Working Hours</p>
            </div>
            <div className="text-center p-4 rounded-lg bg-secondary-50 dark:bg-secondary-700">
              <MapPin className="w-8 h-8 text-warning-500 mx-auto mb-2" />
              <p className="font-medium text-secondary-900 dark:text-white">
                {attendanceStatus.data.attendance?.locationStatus === 'valid' ? 'Office' :
                 attendanceStatus.data.attendance?.locationStatus === 'remote' ? 'Remote' : 'Unknown'}
              </p>
              <p className="text-sm text-secondary-600 dark:text-secondary-400">Location</p>
            </div>
          </div>
        </div>
      )}

      {/* Filters */}
      <div className="card">
        <h3 className="text-lg font-semibold text-secondary-900 dark:text-white mb-4">
          Filters
        </h3>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
          <Input
            label="Date"
            name="date"
            type="date"
            value={filters.date}
            onChange={(e) => handleFilterChange('date', e.target.value)}
          />
          <Select
            label="Status"
            name="status"
            options={[
              { value: '', label: 'All Statuses' },
              { value: 'present', label: 'Present' },
              { value: 'late', label: 'Late' },
              { value: 'absent', label: 'Absent' },
              { value: 'remote', label: 'Remote' },
              { value: 'invalid', label: 'Invalid' },
            ]}
            value={filters.status}
            onChange={(e) => handleFilterChange('status', e.target.value)}
          />
        </div>
        
        {/* Department Filter */}
        <div className="mb-4">
          <label className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-2">
            Departments
          </label>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
            {['IT', 'HR', 'Finance', 'Marketing', 'Sales', 'Operations'].map((dept) => (
              <Checkbox
                key={dept}
                name={`dept-${dept}`}
                label={dept}
                checked={filters.departments.includes(dept)}
                onChange={(e) => {
                  if (e.target.checked) {
                    setFilters(prev => ({
                      ...prev,
                      departments: [...prev.departments, dept],
                      page: 1
                    }));
                  } else {
                    setFilters(prev => ({
                      ...prev,
                      departments: prev.departments.filter(d => d !== dept),
                      page: 1
                    }));
                  }
                }}
              />
            ))}
          </div>
        </div>
        
        <div className="flex justify-end">
          <Button
            variant="outline"
            onClick={() => setFilters({ date: '', status: '', departments: [], page: 1 })}
          >
            <Filter className="w-4 h-4 mr-2" />
            Clear Filters
          </Button>
        </div>
      </div>

      {/* Attendance table */}
      <div className="card">
        <div className="flex items-center justify-between mb-4">
          <h3 className="text-lg font-semibold text-secondary-900 dark:text-white">
            {isAdmin ? 'All Attendance Records' : 'Your Attendance History'}
          </h3>
          <Button variant="outline" size="sm">
            <Download className="w-4 h-4 mr-2" />
            Export
          </Button>
        </div>
        
        <Table
          columns={columns}
          data={(currentData?.attendances || []).map(attendance => ({
            ...attendance,
            workingHours: attendance.workingHours && typeof attendance.workingHours === 'number' ? attendance.workingHours : null
          }))}
          loading={isLoading}
          pagination={currentData?.pagination}
          onPageChange={handlePageChange}
        />
      </div>

      {/* Manual Entry Modal */}
      <ManualEntryModal
        isOpen={isManualEntryModalOpen}
        onClose={() => setIsManualEntryModalOpen(false)}
        onSubmit={(data) => manualEntryMutation.mutate(data)}
        isLoading={manualEntryMutation.isLoading}
      />

      {/* Edit Attendance Modal */}
      <EditAttendanceModal
        isOpen={isEditModalOpen}
        onClose={() => {
          setIsEditModalOpen(false);
          setSelectedAttendance(null);
        }}
        attendance={selectedAttendance}
        onSubmit={(data) => updateAttendanceMutation.mutate(data)}
        isLoading={updateAttendanceMutation.isLoading}
      />
    </div>
  );
};

// Manual Entry Modal Component
const ManualEntryModal: React.FC<{
  isOpen: boolean;
  onClose: () => void;
  onSubmit: (data: any) => void;
  isLoading: boolean;
}> = ({ isOpen, onClose, onSubmit, isLoading }) => {
  const [formData, setFormData] = useState({
    date: new Date().toISOString().split('T')[0],
    clockInTime: '',
    clockOutTime: '',
    status: 'present',
    notes: '',
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSubmit(formData);
  };

  return (
    <Modal isOpen={isOpen} onClose={onClose} title="Manual Attendance Entry" size="lg">
      <form onSubmit={handleSubmit} className="space-y-4">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <Input
            label="Date"
            name="date"
            type="date"
            value={formData.date}
            onChange={(e) => setFormData({ ...formData, date: e.target.value })}
            required
          />
          <Select
            label="Status"
            name="status"
            options={[
              { value: 'present', label: 'Present' },
              { value: 'late', label: 'Late' },
              { value: 'absent', label: 'Absent' },
              { value: 'remote', label: 'Remote' },
            ]}
            value={formData.status}
            onChange={(e) => setFormData({ ...formData, status: e.target.value })}
            required
          />
          <Input
            label="Clock In Time"
            name="clockInTime"
            type="time"
            value={formData.clockInTime}
            onChange={(e) => setFormData({ ...formData, clockInTime: e.target.value })}
          />
          <Input
            label="Clock Out Time"
            name="clockOutTime"
            type="time"
            value={formData.clockOutTime}
            onChange={(e) => setFormData({ ...formData, clockOutTime: e.target.value })}
          />
        </div>
        <Input
          label="Notes"
          name="notes"
          placeholder="Additional notes or reason for manual entry"
          value={formData.notes}
          onChange={(e) => setFormData({ ...formData, notes: e.target.value })}
        />
        <div className="flex gap-3 pt-4">
          <Button
            type="button"
            variant="outline"
            onClick={onClose}
            className="flex-1"
          >
            Cancel
          </Button>
          <Button
            type="submit"
            variant="primary"
            loading={isLoading}
            className="flex-1"
          >
            <Plus className="w-4 h-4 mr-2" />
            Create Entry
          </Button>
        </div>
      </form>
    </Modal>
  );
};

// Edit Attendance Modal Component
const EditAttendanceModal: React.FC<{
  isOpen: boolean;
  onClose: () => void;
  attendance: any;
  onSubmit: (data: any) => void;
  isLoading: boolean;
}> = ({ isOpen, onClose, attendance, onSubmit, isLoading }) => {
  const [formData, setFormData] = useState({
    clockInTime: '',
    clockOutTime: '',
    status: 'present',
    notes: '',
  });

  React.useEffect(() => {
    if (attendance) {
      setFormData({
        clockInTime: attendance.clockInTime ? attendance.clockInTime.split('T')[1].substring(0, 5) : '',
        clockOutTime: attendance.clockOutTime ? attendance.clockOutTime.split('T')[1].substring(0, 5) : '',
        status: attendance.status || 'present',
        notes: attendance.notes || '',
      });
    }
  }, [attendance]);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSubmit(formData);
  };

  if (!attendance) return null;

  return (
    <Modal isOpen={isOpen} onClose={onClose} title="Edit Attendance Record" size="lg">
      <form onSubmit={handleSubmit} className="space-y-4">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <Input
            label="Clock In Time"
            name="clockInTime"
            type="time"
            value={formData.clockInTime}
            onChange={(e) => setFormData({ ...formData, clockInTime: e.target.value })}
          />
          <Input
            label="Clock Out Time"
            name="clockOutTime"
            type="time"
            value={formData.clockOutTime}
            onChange={(e) => setFormData({ ...formData, clockOutTime: e.target.value })}
          />
          <Select
            label="Status"
            name="status"
            options={[
              { value: 'present', label: 'Present' },
              { value: 'late', label: 'Late' },
              { value: 'absent', label: 'Absent' },
              { value: 'remote', label: 'Remote' },
              { value: 'invalid', label: 'Invalid' },
            ]}
            value={formData.status}
            onChange={(e) => setFormData({ ...formData, status: e.target.value })}
            required
          />
        </div>
        <Input
          label="Notes"
          name="notes"
          placeholder="Additional notes"
          value={formData.notes}
          onChange={(e) => setFormData({ ...formData, notes: e.target.value })}
        />
        <div className="flex gap-3 pt-4">
          <Button
            type="button"
            variant="outline"
            onClick={onClose}
            className="flex-1"
          >
            Cancel
          </Button>
          <Button
            type="submit"
            variant="primary"
            loading={isLoading}
            className="flex-1"
          >
            <Edit className="w-4 h-4 mr-2" />
            Update Record
          </Button>
        </div>
      </form>
    </Modal>
  );
};

export default Attendance;
