import React, { useState } from 'react';
import { useQuery } from 'react-query';
import { 
  Calendar, 
  Download, 
  Filter, 
  Search,
  Building,
  Users,
  Clock,
  TrendingUp,
  BarChart3,
  PieChart
} from 'lucide-react';
import { reportsAPI } from '@/lib/api';
import Button from '@/components/ui/Button';
import Input from '@/components/ui/Input';
import Select from '@/components/ui/Select';
import Modal from '@/components/ui/Modal';
import Table from '@/components/ui/Table';
import LoadingSpinner from '@/components/LoadingSpinner';
import { cn, formatTime } from '@/utils';
import Checkbox from '@/components/ui/Checkbox';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
  ArcElement,
  PointElement,
  LineElement,
} from 'chart.js';
import { Bar, Doughnut, Line } from 'react-chartjs-2';

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
  ArcElement,
  PointElement,
  LineElement
);

const Reports: React.FC = () => {
  const [selectedStat, setSelectedStat] = useState<string | null>(null);
  const [isStatsModalOpen, setIsStatsModalOpen] = useState(false);
  const [filters, setFilters] = useState({
    startDate: new Date(Date.now() - 30 * 24 * 60 * 60 * 1000).toISOString().split('T')[0], // 30 days ago
    endDate: new Date().toISOString().split('T')[0],
    departments: [] as string[],
    geofence: '',
    reportType: 'attendance',
  });

  // Fetch dashboard data for overview
  const { data: dashboardData, isLoading: isDashboardLoading } = useQuery(
    ['dashboard'],
    reportsAPI.getDashboard,
    {
      refetchInterval: 300000, // Refetch every 5 minutes
    }
  );

  // Fetch attendance report
  const { data: attendanceReport, isLoading: isAttendanceLoading } = useQuery(
    ['attendance-report', filters],
    () => reportsAPI.getAttendance(filters),
    {
      keepPreviousData: true,
    }
  );

  const handleFilterChange = (key: string, value: string) => {
    setFilters(prev => ({ ...prev, [key]: value }));
  };

  const handleExport = (type: string) => {
    // In a real app, this would trigger CSV/PDF export
    alert(`Exporting ${type} report...`);
  };

  const generateChartData = (data: any) => {
    if (!data) return [];
    
    return [
      { name: 'Present', value: data.present || 0, color: '#10B981' },
      { name: 'Late', value: data.late || 0, color: '#F59E0B' },
      { name: 'Absent', value: data.absent || 0, color: '#EF4444' },
      { name: 'Remote', value: data.remote || 0, color: '#8B5CF6' },
      { name: 'Invalid', value: data.invalid || 0, color: '#6B7280' },
    ];
  };

  const generateWeeklyData = (data: any) => {
    if (!data) return [];
    
    // Mock weekly data - in real app this would come from API
    return [
      { name: 'Mon', present: 45, late: 8, absent: 12 },
      { name: 'Tue', present: 48, late: 5, absent: 10 },
      { name: 'Wed', present: 42, late: 10, absent: 15 },
      { name: 'Thu', present: 50, late: 3, absent: 8 },
      { name: 'Fri', present: 46, late: 7, absent: 11 },
    ];
  };

  return (
    <div className="space-y-6">
      {/* Page header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-secondary-900 dark:text-white">
            Reports
          </h1>
          <p className="text-secondary-600 dark:text-secondary-400">
            Generate and view attendance reports
          </p>
        </div>
        <div className="flex items-center gap-3">
          <Button variant="outline" size="sm">
            <Download className="w-4 h-4 mr-2" />
            Export All
          </Button>
        </div>
      </div>

      {/* Filters */}
      <div className="card">
        <h3 className="text-lg font-semibold text-secondary-900 dark:text-white mb-4">
          Report Filters
        </h3>
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-4">
          <Input
            label="Start Date"
            name="startDate"
            type="date"
            value={filters.startDate}
            onChange={(e) => handleFilterChange('startDate', e.target.value)}
          />
          <Input
            label="End Date"
            name="endDate"
            type="date"
            value={filters.endDate}
            onChange={(e) => handleFilterChange('endDate', e.target.value)}
          />
          <Select
            label="Geofence"
            name="geofence"
            options={[
              { value: '', label: 'All Geofences' },
              { value: 'office', label: 'Office' },
              { value: 'branch1', label: 'Branch 1' },
              { value: 'branch2', label: 'Branch 2' },
            ]}
            value={filters.geofence}
            onChange={(e) => handleFilterChange('geofence', e.target.value)}
          />
          <Select
            label="Report Type"
            name="reportType"
            options={[
              { value: 'attendance', label: 'Attendance' },
              { value: 'department', label: 'Department' },
              { value: 'user', label: 'User' },
              { value: 'geofence', label: 'Geofence' },
            ]}
            value={filters.reportType}
            onChange={(e) => handleFilterChange('reportType', e.target.value)}
          />
        </div>
        
        {/* Department Filter */}
        <div className="mb-4">
          <label className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-2">
            Departments
          </label>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
            {['IT', 'HR', 'Finance', 'Marketing', 'Sales', 'Operations'].map((dept) => (
              <Checkbox
                key={dept}
                name={`dept-${dept}`}
                label={dept}
                checked={filters.departments.includes(dept)}
                onChange={(e) => {
                  if (e.target.checked) {
                    setFilters(prev => ({
                      ...prev,
                      departments: [...prev.departments, dept]
                    }));
                  } else {
                    setFilters(prev => ({
                      ...prev,
                      departments: prev.departments.filter(d => d !== dept)
                    }));
                  }
                }}
              />
            ))}
          </div>
        </div>
        
        <div className="flex justify-end">
          <Button
            variant="outline"
            onClick={() => setFilters({
              startDate: new Date(Date.now() - 30 * 24 * 60 * 60 * 1000).toISOString().split('T')[0],
              endDate: new Date().toISOString().split('T')[0],
              departments: [],
              geofence: '',
              reportType: 'attendance',
            })}
          >
            <Filter className="w-4 h-4 mr-2" />
            Clear Filters
          </Button>
        </div>
      </div>

      {/* Overview Stats */}
      {dashboardData?.data && (
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
          <div 
            className="stat-card cursor-pointer hover:shadow-lg transition-shadow duration-200"
            onClick={() => {
              setSelectedStat('totalEmployees');
              setIsStatsModalOpen(true);
            }}
          >
            <div className="flex items-center justify-between">
              <div>
                <p className="stat-label">Total Employees</p>
                <p className="stat-value">{dashboardData.data.totalEmployees || 0}</p>
              </div>
              <div className="w-12 h-12 rounded-lg bg-primary-500 flex items-center justify-center">
                <Users className="w-6 h-6 text-white" />
              </div>
            </div>
          </div>
          <div 
            className="stat-card cursor-pointer hover:shadow-lg transition-shadow duration-200"
            onClick={() => {
              setSelectedStat('present');
              setIsStatsModalOpen(true);
            }}
          >
            <div className="flex items-center justify-between">
              <div>
                <p className="stat-label">Present Today</p>
                <p className="stat-value">{dashboardData.data.today?.present || 0}</p>
              </div>
              <div className="w-12 h-12 rounded-lg bg-success-500 flex items-center justify-center">
                <Clock className="w-6 h-6 text-white" />
              </div>
            </div>
          </div>
          <div 
            className="stat-card cursor-pointer hover:shadow-lg transition-shadow duration-200"
            onClick={() => {
              setSelectedStat('late');
              setIsStatsModalOpen(true);
            }}
          >
            <div className="flex items-center justify-between">
              <div>
                <p className="stat-label">Late Today</p>
                <p className="stat-value">{dashboardData.data.today?.late || 0}</p>
              </div>
              <div className="w-12 h-12 rounded-lg bg-warning-500 flex items-center justify-center">
                <TrendingUp className="w-6 h-6 text-white" />
              </div>
            </div>
          </div>
          <div 
            className="stat-card cursor-pointer hover:shadow-lg transition-shadow duration-200"
            onClick={() => {
              setSelectedStat('absent');
              setIsStatsModalOpen(true);
            }}
          >
            <div className="flex items-center justify-between">
              <div>
                <p className="stat-label">Absent Today</p>
                <p className="stat-value">{dashboardData.data.today?.absent || 0}</p>
              </div>
              <div className="w-12 h-12 rounded-lg bg-danger-500 flex items-center justify-center">
                <Calendar className="w-6 h-6 text-white" />
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Charts Section */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Attendance Distribution Chart */}
        <div className="card">
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-semibold text-secondary-900 dark:text-white">
              Attendance Distribution
            </h3>
            <Button variant="outline" size="sm" onClick={() => handleExport('attendance-distribution')}>
              <Download className="w-4 h-4 mr-2" />
              Export
            </Button>
          </div>
          <div className="h-64">
            <Doughnut
              data={{
                labels: ['Present', 'Late', 'Absent', 'Remote', 'Invalid'],
                datasets: [
                  {
                    data: [
                      dashboardData?.data?.today?.present || 0,
                      dashboardData?.data?.today?.late || 0,
                      dashboardData?.data?.today?.absent || 0,
                      dashboardData?.data?.today?.remote || 0,
                      dashboardData?.data?.today?.invalid || 0,
                    ],
                    backgroundColor: [
                      'rgb(34, 197, 94)',   // green
                      'rgb(251, 191, 36)',  // yellow
                      'rgb(239, 68, 68)',   // red
                      'rgb(139, 92, 246)',  // purple
                      'rgb(107, 114, 128)', // gray
                    ],
                    borderWidth: 2,
                    borderColor: 'white',
                  },
                ],
              }}
              options={{
                responsive: true,
                maintainAspectRatio: false,
                plugins: {
                  legend: {
                    position: 'bottom',
                    labels: {
                      color: 'rgb(107, 114, 128)',
                      font: {
                        size: 12,
                      },
                    },
                  },
                  tooltip: {
                    callbacks: {
                      label: function(context) {
                        const label = context.label || '';
                        const value = context.parsed;
                        const total = (dashboardData?.data?.today?.present || 0) + 
                                    (dashboardData?.data?.today?.late || 0) + 
                                    (dashboardData?.data?.today?.absent || 0) + 
                                    (dashboardData?.data?.today?.remote || 0) + 
                                    (dashboardData?.data?.today?.invalid || 0);
                        const percentage = total > 0 ? Math.round((value / total) * 100) : 0;
                        return `${label}: ${value} (${percentage}%)`;
                      }
                    }
                  }
                }
              }}
            />
          </div>
        </div>

        {/* Weekly Trend Chart */}
        <div className="card">
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-semibold text-secondary-900 dark:text-white">
              Weekly Trend
            </h3>
            <Button variant="outline" size="sm" onClick={() => handleExport('weekly-trend')}>
              <Download className="w-4 h-4 mr-2" />
              Export
            </Button>
          </div>
          <div className="h-64">
            <Bar
              data={{
                labels: generateWeeklyData(dashboardData?.data?.week).map(item => item.name),
                datasets: [
                  {
                    label: 'Present',
                    data: generateWeeklyData(dashboardData?.data?.week).map(item => item.present),
                    backgroundColor: 'rgb(34, 197, 94)',
                    borderRadius: 4,
                  },
                  {
                    label: 'Late',
                    data: generateWeeklyData(dashboardData?.data?.week).map(item => item.late),
                    backgroundColor: 'rgb(251, 191, 36)',
                    borderRadius: 4,
                  },
                  {
                    label: 'Absent',
                    data: generateWeeklyData(dashboardData?.data?.week).map(item => item.absent),
                    backgroundColor: 'rgb(239, 68, 68)',
                    borderRadius: 4,
                  },
                ],
              }}
              options={{
                responsive: true,
                maintainAspectRatio: false,
                plugins: {
                  legend: {
                    position: 'bottom',
                    labels: {
                      color: 'rgb(107, 114, 128)',
                      font: {
                        size: 12,
                      },
                    },
                  },
                  tooltip: {
                    mode: 'index',
                    intersect: false,
                  },
                },
                scales: {
                  x: {
                    grid: {
                      color: 'rgb(229, 231, 235)',
                    },
                    ticks: {
                      color: 'rgb(107, 114, 128)',
                    },
                  },
                  y: {
                    beginAtZero: true,
                    grid: {
                      color: 'rgb(229, 231, 235)',
                    },
                    ticks: {
                      color: 'rgb(107, 114, 128)',
                    },
                  },
                },
              }}
            />
          </div>
        </div>
      </div>

      {/* Additional Charts Section */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Department Performance Chart */}
        <div className="card">
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-semibold text-secondary-900 dark:text-white">
              Department Performance
            </h3>
            <Button variant="outline" size="sm" onClick={() => handleExport('department-performance')}>
              <Download className="w-4 h-4 mr-2" />
              Export
            </Button>
          </div>
          <div className="h-64">
            <Bar
              data={{
                labels: Object.keys(dashboardData?.data?.departmentStats || {}),
                datasets: [
                  {
                    label: 'Present',
                    data: Object.values(dashboardData?.data?.departmentStats || {}).map((dept: any) => dept.present || 0),
                    backgroundColor: 'rgb(34, 197, 94)',
                    borderRadius: 4,
                  },
                  {
                    label: 'Late',
                    data: Object.values(dashboardData?.data?.departmentStats || {}).map((dept: any) => dept.late || 0),
                    backgroundColor: 'rgb(251, 191, 36)',
                    borderRadius: 4,
                  },
                  {
                    label: 'Absent',
                    data: Object.values(dashboardData?.data?.departmentStats || {}).map((dept: any) => dept.absent || 0),
                    backgroundColor: 'rgb(239, 68, 68)',
                    borderRadius: 4,
                  },
                ],
              }}
              options={{
                responsive: true,
                maintainAspectRatio: false,
                plugins: {
                  legend: {
                    position: 'bottom',
                    labels: {
                      color: 'rgb(107, 114, 128)',
                      font: {
                        size: 12,
                      },
                    },
                  },
                  tooltip: {
                    mode: 'index',
                    intersect: false,
                  },
                },
                scales: {
                  x: {
                    grid: {
                      color: 'rgb(229, 231, 235)',
                    },
                    ticks: {
                      color: 'rgb(107, 114, 128)',
                    },
                  },
                  y: {
                    beginAtZero: true,
                    grid: {
                      color: 'rgb(229, 231, 235)',
                    },
                    ticks: {
                      color: 'rgb(107, 114, 128)',
                    },
                  },
                },
              }}
            />
          </div>
        </div>

        {/* Monthly Attendance Trend */}
        <div className="card">
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-semibold text-secondary-900 dark:text-white">
              Monthly Attendance Trend
            </h3>
            <Button variant="outline" size="sm" onClick={() => handleExport('monthly-trend')}>
              <Download className="w-4 h-4 mr-2" />
              Export
            </Button>
          </div>
          <div className="h-64">
            <Line
              data={{
                labels: ['Week 1', 'Week 2', 'Week 3', 'Week 4'],
                datasets: [
                  {
                    label: 'Attendance Rate',
                    data: [85, 88, 92, 90], // Sample data - replace with real data
                    borderColor: 'rgb(59, 130, 246)',
                    backgroundColor: 'rgba(59, 130, 246, 0.1)',
                    tension: 0.4,
                    fill: true,
                  },
                  {
                    label: 'Late Rate',
                    data: [12, 10, 8, 9], // Sample data - replace with real data
                    borderColor: 'rgb(251, 191, 36)',
                    backgroundColor: 'rgba(251, 191, 36, 0.1)',
                    tension: 0.4,
                    fill: true,
                  },
                ],
              }}
              options={{
                responsive: true,
                maintainAspectRatio: false,
                plugins: {
                  legend: {
                    position: 'bottom',
                    labels: {
                      color: 'rgb(107, 114, 128)',
                      font: {
                        size: 12,
                      },
                    },
                  },
                  tooltip: {
                    mode: 'index',
                    intersect: false,
                  },
                },
                scales: {
                  x: {
                    grid: {
                      color: 'rgb(229, 231, 235)',
                    },
                    ticks: {
                      color: 'rgb(107, 114, 128)',
                    },
                  },
                  y: {
                    beginAtZero: true,
                    max: 100,
                    grid: {
                      color: 'rgb(229, 231, 235)',
                    },
                    ticks: {
                      color: 'rgb(107, 114, 128)',
                      callback: function(value) {
                        return value + '%';
                      }
                    },
                  },
                },
              }}
            />
          </div>
        </div>
      </div>

      {/* Department Overview */}
      {dashboardData?.data?.departmentStats && (
        <div className="card">
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-semibold text-secondary-900 dark:text-white">
              Department Overview
            </h3>
            <Button variant="outline" size="sm" onClick={() => handleExport('department-overview')}>
              <Download className="w-4 h-4 mr-2" />
              Export
            </Button>
          </div>
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-secondary-50 dark:bg-secondary-700">
                <tr>
                  <th className="px-4 py-3 text-left text-xs font-medium text-secondary-500 dark:text-secondary-400 uppercase tracking-wider">
                    Department
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-secondary-500 dark:text-secondary-400 uppercase tracking-wider">
                    Total Users
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-secondary-500 dark:text-secondary-400 uppercase tracking-wider">
                    Present
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-secondary-500 dark:text-secondary-400 uppercase tracking-wider">
                    Late
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-secondary-500 dark:text-secondary-400 uppercase tracking-wider">
                    Absent
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-secondary-500 dark:text-secondary-400 uppercase tracking-wider">
                    Attendance Rate
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white dark:bg-secondary-800 divide-y divide-secondary-200 dark:divide-secondary-700">
                {Object.entries(dashboardData.data.departmentStats).map(([dept, stats]: [string, any]) => (
                  <tr key={dept} className="hover:bg-secondary-50 dark:hover:bg-secondary-700">
                    <td className="px-4 py-3 text-sm text-secondary-900 dark:text-white">
                      <div className="flex items-center gap-2">
                        <Building className="w-4 h-4 text-secondary-500" />
                        {dept}
                      </div>
                    </td>
                    <td className="px-4 py-3 text-sm text-secondary-900 dark:text-white">
                      {stats.totalUsers}
                    </td>
                    <td className="px-4 py-3 text-sm text-secondary-900 dark:text-white">
                      <span className="badge badge-success">{stats.present}</span>
                    </td>
                    <td className="px-4 py-3 text-sm text-secondary-900 dark:text-white">
                      <span className="badge badge-warning">{stats.late}</span>
                    </td>
                    <td className="px-4 py-3 text-sm text-secondary-900 dark:text-white">
                      <span className="badge badge-danger">{stats.absent}</span>
                    </td>
                    <td className="px-4 py-3 text-sm text-secondary-900 dark:text-white">
                      {Math.round(((stats.present + stats.late) / stats.totalUsers) * 100)}%
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}

      {/* Recent Attendance */}
      {dashboardData?.data?.recentAttendances && (
        <div className="card">
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-semibold text-secondary-900 dark:text-white">
              Recent Attendance
            </h3>
            <Button variant="outline" size="sm" onClick={() => handleExport('recent-attendance')}>
              <Download className="w-4 h-4 mr-2" />
              Export
            </Button>
          </div>
          <div className="space-y-3">
            {dashboardData.data.recentAttendances.slice(0, 10).map((attendance: any) => (
              <div key={attendance.id} className="flex items-center gap-3 p-3 rounded-lg bg-secondary-50 dark:bg-secondary-700">
                <div className="w-8 h-8 rounded-full bg-primary-100 dark:bg-primary-900 flex items-center justify-center">
                  <span className="text-sm font-medium text-primary-700 dark:text-primary-300">
                    {attendance.user?.name?.charAt(0).toUpperCase()}
                  </span>
                </div>
                <div className="flex-1">
                  <p className="font-medium text-secondary-900 dark:text-white">
                    {attendance.user?.name}
                  </p>
                  <p className="text-sm text-secondary-500 dark:text-secondary-400">
                    {attendance.clockInTime ? `Clocked in at ${formatTime(attendance.clockInTime)}` : 'No clock in'}
                  </p>
                </div>
                <div className="flex items-center gap-2">
                  <span className={cn(
                    'badge',
                    attendance.status === 'present' ? 'badge-success' :
                    attendance.status === 'late' ? 'badge-warning' :
                    attendance.status === 'absent' ? 'badge-danger' :
                    attendance.status === 'remote' ? 'badge-secondary' : 'badge-danger'
                  )}>
                    {attendance.status}
                  </span>
                  <span className={cn(
                    'badge',
                    attendance.locationStatus === 'valid' ? 'badge-success' :
                    attendance.locationStatus === 'remote' ? 'badge-warning' : 'badge-danger'
                  )}>
                    {attendance.locationStatus === 'valid' ? 'Office' : 
                     attendance.locationStatus === 'remote' ? 'Remote' : 'Invalid'}
                  </span>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Quick Actions */}
      <div className="card">
        <h3 className="text-lg font-semibold text-secondary-900 dark:text-white mb-4">
          Quick Actions
        </h3>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <Button
            variant="outline"
            className="h-20 flex flex-col items-center justify-center gap-2"
            onClick={() => handleExport('comprehensive')}
          >
            <Download className="w-6 h-6" />
            <span>Comprehensive Report</span>
          </Button>
          <Button
            variant="outline"
            className="h-20 flex flex-col items-center justify-center gap-2"
            onClick={() => handleExport('summary')}
          >
            <BarChart3 className="w-6 h-6" />
            <span>Summary Report</span>
          </Button>
          <Button
            variant="outline"
            className="h-20 flex flex-col items-center justify-center gap-2"
            onClick={() => handleExport('detailed')}
          >
            <TrendingUp className="w-6 h-6" />
            <span>Detailed Analysis</span>
          </Button>
        </div>
      </div>

      {/* Stats Detail Modal */}
      <Modal
        isOpen={isStatsModalOpen}
        onClose={() => {
          setIsStatsModalOpen(false);
          setSelectedStat(null);
        }}
        title={getReportsStatsModalTitle(selectedStat)}
        size="xl"
      >
        <div className="space-y-4">
          <ReportsStatsDetailTable statKey={selectedStat} data={dashboardData?.data} />
        </div>
      </Modal>
    </div>
  );
};

const getReportsStatsModalTitle = (statKey: string | null): string => {
  switch (statKey) {
    case 'totalEmployees':
      return 'All Employees';
    case 'present':
      return 'Present Today';
    case 'late':
      return 'Late Today';
    case 'absent':
      return 'Absent Today';
    default:
      return 'Details';
  }
};

const ReportsStatsDetailTable: React.FC<{ statKey: string | null; data: any }> = ({ statKey, data }) => {
  if (!statKey || !data) return null;

  const getTableData = () => {
    switch (statKey) {
      case 'totalEmployees':
        return data.users || [];
      case 'present':
        return data.presentUsers || [];
      case 'late':
        return data.lateUsers || [];
      case 'absent':
        return data.absentUsers || [];
      default:
        return [];
    }
  };

  const getColumns = () => {
    const baseColumns = [
      {
        key: 'name',
        label: 'Name',
        render: (value: string, row: any) => (
          <div className="flex items-center gap-3">
            <div className="w-8 h-8 rounded-full bg-primary-100 dark:bg-primary-900 flex items-center justify-center">
              <span className="text-sm font-medium text-primary-700 dark:text-primary-300">
                {value?.charAt(0).toUpperCase()}
              </span>
            </div>
            <span className="font-medium">{value}</span>
          </div>
        ),
      },
      {
        key: 'department',
        label: 'Department',
        render: (value: string) => value || 'Not assigned',
      },
      {
        key: 'role',
        label: 'Role',
        render: (value: string) => (
          <span className={cn(
            'badge',
            value === 'admin' ? 'badge-warning' : 'badge-success'
          )}>
            {value === 'admin' ? 'Administrator' : 'Employee'}
          </span>
        ),
      },
    ];

    if (statKey === 'present' || statKey === 'late') {
      baseColumns.push({
        key: 'clockInTime',
        label: 'Clock In Time',
        render: (value: string) => value ? formatTime(value) : '-',
      });
    }

    return baseColumns;
  };

  const tableData = getTableData();
  const columns = getColumns();

  return (
    <div>
      <div className="mb-4">
        <p className="text-secondary-600 dark:text-secondary-400">
          Showing {tableData.length} {statKey === 'totalEmployees' ? 'employees' : 'users'} 
          {statKey !== 'totalEmployees' && ` (${statKey} today)`}
        </p>
      </div>
      
      <Table
        columns={columns}
        data={tableData}
        loading={false}
      />
    </div>
  );
};

export default Reports;
