import React, { useState } from 'react';
import { useQuery, useMutation, useQueryClient } from 'react-query';
import { 
  Users as UsersIcon, 
  Plus, 
  Edit, 
  Trash2, 
  Eye, 
  MoreHorizontal,
  Building,
  Mail,
  Calendar,
  Shield,
  CheckCircle,
  XCircle,
  Search,
  Filter,
  Download
} from 'lucide-react';
import { usersAPI } from '@/lib/api';
import Button from '@/components/ui/Button';
import Input from '@/components/ui/Input';
import Select from '@/components/ui/Select';
import Modal from '@/components/ui/Modal';
import Table from '@/components/ui/Table';
import LoadingSpinner from '@/components/LoadingSpinner';
import { cn, formatDate } from '@/utils';
import Checkbox from '@/components/ui/Checkbox';

const Users: React.FC = () => {
  const queryClient = useQueryClient();
  const [isCreateModalOpen, setIsCreateModalOpen] = useState(false);
  const [isEditModalOpen, setIsEditModalOpen] = useState(false);
  const [isViewModalOpen, setIsViewModalOpen] = useState(false);
  const [selectedUser, setSelectedUser] = useState<any>(null);
  const [selectedStat, setSelectedStat] = useState<string | null>(null);
  const [isStatsModalOpen, setIsStatsModalOpen] = useState(false);
  const [filters, setFilters] = useState({
    search: '',
    role: '',
    departments: [] as string[],
    status: '',
    page: 1,
  });

  // Fetch users
  const { data: usersData, isLoading } = useQuery(
    ['users', filters],
    () => usersAPI.getAll(filters),
    {
      keepPreviousData: true,
    }
  );

  // Create user mutation
  const createUserMutation = useMutation(
    (data: any) => usersAPI.create(data),
    {
      onSuccess: (response) => {
        queryClient.invalidateQueries(['users']);
        setIsCreateModalOpen(false);
        // Show success message with temp password
        alert(`User created successfully! Temporary password: ${response.data.tempPassword}`);
      },
      onError: (error: any) => {
        console.error('Create user failed:', error);
        alert('Failed to create user. Please try again.');
      },
    }
  );

  // Update user mutation
  const updateUserMutation = useMutation(
    (data: any) => usersAPI.update(selectedUser?.id || '', data),
    {
      onSuccess: () => {
        queryClient.invalidateQueries(['users']);
        setIsEditModalOpen(false);
        setSelectedUser(null);
      },
      onError: (error: any) => {
        console.error('Update user failed:', error);
        alert('Failed to update user. Please try again.');
      },
    }
  );

  // Delete user mutation
  const deleteUserMutation = useMutation(
    (id: string) => usersAPI.delete(id),
    {
      onSuccess: () => {
        queryClient.invalidateQueries(['users']);
      },
      onError: (error: any) => {
        console.error('Delete user failed:', error);
        alert('Failed to delete user. Please try again.');
      },
    }
  );

  // Update user status mutation
  const updateStatusMutation = useMutation(
    ({ id, isActive }: { id: string; isActive: boolean }) => usersAPI.updateStatus(id, isActive),
    {
      onSuccess: () => {
        queryClient.invalidateQueries(['users']);
      },
      onError: (error: any) => {
        console.error('Update status failed:', error);
        alert('Failed to update user status. Please try again.');
      },
    }
  );

  const handleCreate = () => {
    setIsCreateModalOpen(true);
  };

  const handleEdit = (user: any) => {
    setSelectedUser(user);
    setIsEditModalOpen(true);
  };

  const handleView = (user: any) => {
    setSelectedUser(user);
    setIsViewModalOpen(true);
  };

  const handleDelete = (id: string) => {
    if (window.confirm('Are you sure you want to delete this user? This action cannot be undone.')) {
      deleteUserMutation.mutate(id);
    }
  };

  const handleStatusToggle = (id: string, currentStatus: boolean) => {
    updateStatusMutation.mutate({ id, isActive: !currentStatus });
  };

  const handlePageChange = (page: number) => {
    setFilters(prev => ({ ...prev, page }));
  };

  const handleFilterChange = (key: string, value: string) => {
    setFilters(prev => ({ ...prev, [key]: value, page: 1 }));
  };

  const handleSort = (column: string, direction: 'asc' | 'desc') => {
    // Implement sorting logic here
    console.log('Sort:', column, direction);
  };

  // Table columns
  const columns = [
    {
      key: 'name',
      label: 'Name',
      sortable: true,
      render: (value: string, row: any) => (
        <div className="flex items-center gap-3">
          <div className="w-10 h-10 rounded-full bg-primary-100 dark:bg-primary-900 flex items-center justify-center">
            {row.profilePictureUrl ? (
              <img
                src={row.profilePictureUrl}
                alt={value}
                className="w-10 h-10 rounded-full object-cover"
              />
            ) : (
              <span className="text-sm font-medium text-primary-700 dark:text-primary-300">
                {value?.charAt(0).toUpperCase()}
              </span>
            )}
          </div>
          <div>
            <p className="font-medium text-secondary-900 dark:text-white">{value}</p>
            <p className="text-sm text-secondary-500 dark:text-secondary-400">{row.email}</p>
          </div>
        </div>
      ),
    },
    {
      key: 'department',
      label: 'Department',
      render: (value: string) => value || 'Not assigned',
    },
    {
      key: 'role',
      label: 'Role',
      render: (value: string) => (
        <span className={cn(
          'badge',
          value === 'admin' ? 'badge-warning' : 'badge-success'
        )}>
          {value === 'admin' ? 'Administrator' : 'Employee'}
        </span>
      ),
    },
    {
      key: 'isActive',
      label: 'Status',
      render: (value: boolean) => (
        <span className={cn(
          'badge',
          value ? 'badge-success' : 'badge-danger'
        )}>
          {value ? 'Active' : 'Inactive'}
        </span>
      ),
    },
    {
      key: 'lastLoginAt',
      label: 'Last Login',
      render: (value: string) => value ? formatDate(value) : 'Never',
    },
    {
      key: 'createdAt',
      label: 'Joined',
      render: (value: string) => formatDate(value),
    },
    {
      key: 'actions',
      label: 'Actions',
      width: '150px',
      render: (value: any, row: any) => (
        <div className="flex items-center gap-2">
          <button
            onClick={() => handleView(row)}
            className="p-1 text-secondary-500 hover:text-primary-600 transition-colors"
            title="View Details"
          >
            <Eye className="w-4 h-4" />
          </button>
          <button
            onClick={() => handleEdit(row)}
            className="p-1 text-secondary-500 hover:text-warning-600 transition-colors"
            title="Edit User"
          >
            <Edit className="w-4 h-4" />
          </button>
          <button
            onClick={() => handleStatusToggle(row.id, row.isActive)}
            className={cn(
              'p-1 transition-colors',
              row.isActive 
                ? 'text-secondary-500 hover:text-danger-600' 
                : 'text-secondary-500 hover:text-success-600'
            )}
            title={row.isActive ? 'Deactivate User' : 'Activate User'}
          >
            {row.isActive ? <XCircle className="w-4 h-4" /> : <CheckCircle className="w-4 h-4" />}
          </button>
          <button
            onClick={() => handleDelete(row.id)}
            className="p-1 text-secondary-500 hover:text-danger-600 transition-colors"
            title="Delete User"
          >
            <Trash2 className="w-4 h-4" />
          </button>
        </div>
      ),
    },
  ];

  return (
    <div className="space-y-6">
      {/* Page header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-secondary-900 dark:text-white">
            Users
          </h1>
          <p className="text-secondary-600 dark:text-secondary-400">
            Manage system users and employees
          </p>
        </div>
        <div className="flex items-center gap-3">
          <Button
            variant="primary"
            onClick={handleCreate}
          >
            <Plus className="w-4 h-4 mr-2" />
            Add User
          </Button>
          <Button variant="outline" size="sm">
            <Download className="w-4 h-4 mr-2" />
            Export
          </Button>
        </div>
      </div>

      {/* Stats cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <div 
          className="stat-card cursor-pointer hover:shadow-lg transition-shadow duration-200"
          onClick={() => {
            setSelectedStat('total');
            setIsStatsModalOpen(true);
          }}
        >
          <div className="flex items-center justify-between">
            <div>
              <p className="stat-label">Total Users</p>
              <p className="stat-value">{usersData?.data?.pagination?.totalItems || 0}</p>
            </div>
            <div className="w-12 h-12 rounded-lg bg-primary-500 flex items-center justify-center">
              <UsersIcon className="w-6 h-6 text-white" />
            </div>
          </div>
        </div>
        <div 
          className="stat-card cursor-pointer hover:shadow-lg transition-shadow duration-200"
          onClick={() => {
            setSelectedStat('active');
            setIsStatsModalOpen(true);
          }}
        >
          <div className="flex items-center justify-between">
            <div>
              <p className="stat-label">Active Users</p>
              <p className="stat-value">
                {usersData?.data?.users?.filter((u: any) => u.isActive).length || 0}
              </p>
            </div>
            <div className="w-12 h-12 rounded-lg bg-success-500 flex items-center justify-center">
              <CheckCircle className="w-6 h-6 text-white" />
            </div>
          </div>
        </div>
        <div 
          className="stat-card cursor-pointer hover:shadow-lg transition-shadow duration-200"
          onClick={() => {
            setSelectedStat('admins');
            setIsStatsModalOpen(true);
          }}
        >
          <div className="flex items-center justify-between">
            <div>
              <p className="stat-label">Administrators</p>
              <p className="stat-value">
                {usersData?.data?.users?.filter((u: any) => u.role === 'admin').length || 0}
              </p>
            </div>
            <div className="w-12 h-12 rounded-lg bg-warning-500 flex items-center justify-center">
              <Shield className="w-6 h-6 text-white" />
            </div>
          </div>
        </div>
        <div 
          className="stat-card cursor-pointer hover:shadow-lg transition-shadow duration-200"
          onClick={() => {
            setSelectedStat('employees');
            setIsStatsModalOpen(true);
          }}
        >
          <div className="flex items-center justify-between">
            <div>
              <p className="stat-label">Employees</p>
              <p className="stat-value">
                {usersData?.data?.users?.filter((u: any) => u.role === 'employee').length || 0}
              </p>
            </div>
            <div className="w-12 h-12 rounded-lg bg-secondary-500 flex items-center justify-center">
              <Building className="w-6 h-6 text-white" />
            </div>
          </div>
        </div>
      </div>

      {/* Filters */}
      <div className="card">
        <h3 className="text-lg font-semibold text-secondary-900 dark:text-white mb-4">
          Filters
        </h3>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-4">
          <Input
            label="Search"
            name="search"
            placeholder="Search by name or email"
            value={filters.search}
            onChange={(e) => handleFilterChange('search', e.target.value)}
          />
          <Select
            label="Role"
            name="role"
            options={[
              { value: '', label: 'All Roles' },
              { value: 'admin', label: 'Administrator' },
              { value: 'employee', label: 'Employee' },
            ]}
            value={filters.role}
            onChange={(e) => handleFilterChange('role', e.target.value)}
          />
          <Select
            label="Status"
            name="status"
            options={[
              { value: '', label: 'All Statuses' },
              { value: 'true', label: 'Active' },
              { value: 'false', label: 'Inactive' },
            ]}
            value={filters.status}
            onChange={(e) => handleFilterChange('status', e.target.value)}
          />
        </div>
        
        {/* Department Filter */}
        <div className="mb-4">
          <label className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-2">
            Departments
          </label>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
            {['IT', 'HR', 'Finance', 'Marketing', 'Sales', 'Operations'].map((dept) => (
              <Checkbox
                key={dept}
                name={`dept-${dept}`}
                label={dept}
                checked={filters.departments.includes(dept)}
                onChange={(e) => {
                  if (e.target.checked) {
                    setFilters(prev => ({
                      ...prev,
                      departments: [...prev.departments, dept],
                      page: 1
                    }));
                  } else {
                    setFilters(prev => ({
                      ...prev,
                      departments: prev.departments.filter(d => d !== dept),
                      page: 1
                    }));
                  }
                }}
              />
            ))}
          </div>
        </div>
        <div className="flex justify-end mt-4">
          <Button
            variant="outline"
            onClick={() => setFilters({ search: '', role: '', departments: [], status: '', page: 1 })}
          >
            <Filter className="w-4 h-4 mr-2" />
            Clear Filters
          </Button>
        </div>
      </div>

      {/* Users table */}
      <div className="card">
        <Table
          columns={columns}
          data={usersData?.data?.users || []}
          loading={isLoading}
          pagination={usersData?.data?.pagination}
          onPageChange={handlePageChange}
          onSort={handleSort}
        />
      </div>

      {/* Create User Modal */}
      <CreateUserModal
        isOpen={isCreateModalOpen}
        onClose={() => setIsCreateModalOpen(false)}
        onSubmit={(data) => createUserMutation.mutate(data)}
        isLoading={createUserMutation.isLoading}
      />

      {/* Edit User Modal */}
      <EditUserModal
        isOpen={isEditModalOpen}
        onClose={() => {
          setIsEditModalOpen(false);
          setSelectedUser(null);
        }}
        user={selectedUser}
        onSubmit={(data) => updateUserMutation.mutate(data)}
        isLoading={updateUserMutation.isLoading}
      />

      {/* View User Modal */}
      <ViewUserModal
        isOpen={isViewModalOpen}
        onClose={() => {
          setIsViewModalOpen(false);
          setSelectedUser(null);
        }}
        user={selectedUser}
      />

      {/* Stats Detail Modal */}
      <Modal
        isOpen={isStatsModalOpen}
        onClose={() => {
          setIsStatsModalOpen(false);
          setSelectedStat(null);
        }}
        title={getStatsModalTitle(selectedStat)}
        size="xl"
      >
        <div className="space-y-4">
          <StatsDetailTable statKey={selectedStat} usersData={usersData?.data?.users || []} />
        </div>
      </Modal>
    </div>
  );
};

const getStatsModalTitle = (statKey: string | null): string => {
  switch (statKey) {
    case 'total':
      return 'All Users';
    case 'active':
      return 'Active Users';
    case 'admins':
      return 'Administrators';
    case 'employees':
      return 'Employees';
    default:
      return 'User Details';
  }
};

const StatsDetailTable: React.FC<{ statKey: string | null; usersData: any[] }> = ({ statKey, usersData }) => {
  if (!statKey || !usersData) return null;

  const getFilteredData = () => {
    switch (statKey) {
      case 'total':
        return usersData;
      case 'active':
        return usersData.filter((u: any) => u.isActive);
      case 'admins':
        return usersData.filter((u: any) => u.role === 'admin');
      case 'employees':
        return usersData.filter((u: any) => u.role === 'employee');
      default:
        return [];
    }
  };

  const columns = [
    {
      key: 'name',
      label: 'Name',
      render: (value: string, row: any) => (
        <div className="flex items-center gap-3">
          <div className="w-8 h-8 rounded-full bg-primary-100 dark:bg-primary-900 flex items-center justify-center">
            <span className="text-sm font-medium text-primary-700 dark:text-primary-300">
              {value?.charAt(0).toUpperCase()}
            </span>
          </div>
          <span className="font-medium">{value}</span>
        </div>
      ),
    },
    {
      key: 'email',
      label: 'Email',
      render: (value: string) => value,
    },
    {
      key: 'department',
      label: 'Department',
      render: (value: string) => value || 'Not assigned',
    },
    {
      key: 'role',
      label: 'Role',
      render: (value: string) => (
        <span className={cn(
          'badge',
          value === 'admin' ? 'badge-warning' : 'badge-success'
        )}>
          {value === 'admin' ? 'Administrator' : 'Employee'}
        </span>
      ),
    },
    {
      key: 'isActive',
      label: 'Status',
      render: (value: boolean) => (
        <span className={cn(
          'badge',
          value ? 'badge-success' : 'badge-danger'
        )}>
          {value ? 'Active' : 'Inactive'}
        </span>
      ),
    },
  ];

  const filteredData = getFilteredData();

  return (
    <div>
      <div className="mb-4">
        <p className="text-secondary-600 dark:text-secondary-400">
          Showing {filteredData.length} {statKey === 'total' ? 'users' : statKey}
        </p>
      </div>
      
      <Table
        columns={columns}
        data={filteredData}
        loading={false}
      />
    </div>
  );
};

// Create User Modal Component
const CreateUserModal: React.FC<{
  isOpen: boolean;
  onClose: () => void;
  onSubmit: (data: any) => void;
  isLoading: boolean;
}> = ({ isOpen, onClose, onSubmit, isLoading }) => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
    role: 'employee',
    department: '',
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSubmit(formData);
  };

  return (
    <Modal isOpen={isOpen} onClose={onClose} title="Create New User" size="lg">
      <form onSubmit={handleSubmit} className="space-y-4">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <Input
            label="Full Name"
            name="name"
            value={formData.name}
            onChange={(e) => setFormData({ ...formData, name: e.target.value })}
            required
          />
          <Input
            label="Email Address"
            name="email"
            type="email"
            value={formData.email}
            onChange={(e) => setFormData({ ...formData, email: e.target.value })}
            required
          />
          <Input
            label="Password"
            name="password"
            type="password"
            value={formData.password}
            onChange={(e) => setFormData({ ...formData, password: e.target.value })}
            required
          />
          <Select
            label="Role"
            name="role"
            options={[
              { value: 'employee', label: 'Employee' },
              { value: 'admin', label: 'Administrator' },
            ]}
            value={formData.role}
            onChange={(e) => setFormData({ ...formData, role: e.target.value })}
            required
          />
        </div>
        <Select
          label="Department"
          name="department"
          options={[
            { value: '', label: 'Select Department' },
            { value: 'IT', label: 'IT' },
            { value: 'HR', label: 'HR' },
            { value: 'Finance', label: 'Finance' },
            { value: 'Marketing', label: 'Marketing' },
            { value: 'Sales', label: 'Sales' },
          ]}
          value={formData.department}
          onChange={(e) => setFormData({ ...formData, department: e.target.value })}
        />
        <div className="flex gap-3 pt-4">
          <Button
            type="button"
            variant="outline"
            onClick={onClose}
            className="flex-1"
          >
            Cancel
          </Button>
          <Button
            type="submit"
            variant="primary"
            loading={isLoading}
            className="flex-1"
          >
            <Plus className="w-4 h-4 mr-2" />
            Create User
          </Button>
        </div>
      </form>
    </Modal>
  );
};

// Edit User Modal Component
const EditUserModal: React.FC<{
  isOpen: boolean;
  onClose: () => void;
  user: any;
  onSubmit: (data: any) => void;
  isLoading: boolean;
}> = ({ isOpen, onClose, user, onSubmit, isLoading }) => {
  const [formData, setFormData] = useState({
    name: '',
    department: '',
    role: 'employee',
  });

  React.useEffect(() => {
    if (user) {
      setFormData({
        name: user.name || '',
        department: user.department || '',
        role: user.role || 'employee',
      });
    }
  }, [user]);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSubmit(formData);
  };

  if (!user) return null;

  return (
    <Modal isOpen={isOpen} onClose={onClose} title="Edit User" size="lg">
      <form onSubmit={handleSubmit} className="space-y-4">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <Input
            label="Full Name"
            name="name"
            value={formData.name}
            onChange={(e) => setFormData({ ...formData, name: e.target.value })}
            required
          />
          <Select
            label="Role"
            name="role"
            options={[
              { value: 'employee', label: 'Employee' },
              { value: 'admin', label: 'Administrator' },
            ]}
            value={formData.role}
            onChange={(e) => setFormData({ ...formData, role: e.target.value })}
            required
          />
        </div>
        <Select
          label="Department"
          name="department"
          options={[
            { value: '', label: 'Select Department' },
            { value: 'IT', label: 'IT' },
            { value: 'HR', label: 'HR' },
            { value: 'Finance', label: 'Finance' },
            { value: 'Marketing', label: 'Marketing' },
            { value: 'Sales', label: 'Sales' },
          ]}
          value={formData.department}
          onChange={(e) => setFormData({ ...formData, department: e.target.value })}
        />
        <div className="flex gap-3 pt-4">
          <Button
            type="button"
            variant="outline"
            onClick={onClose}
            className="flex-1"
          >
            Cancel
          </Button>
          <Button
            type="submit"
            variant="primary"
            loading={isLoading}
            className="flex-1"
          >
            <Edit className="w-4 h-4 mr-2" />
            Update User
          </Button>
        </div>
      </form>
    </Modal>
  );
};

// View User Modal Component
const ViewUserModal: React.FC<{
  isOpen: boolean;
  onClose: () => void;
  user: any;
}> = ({ isOpen, onClose, user }) => {
  if (!user) return null;

  return (
    <Modal isOpen={isOpen} onClose={onClose} title="User Details" size="lg">
      <div className="space-y-6">
        {/* Profile picture and basic info */}
        <div className="text-center">
          <div className="w-24 h-24 rounded-full bg-primary-100 dark:bg-primary-900 flex items-center justify-center text-2xl font-bold text-primary-700 dark:text-primary-300 mx-auto mb-4">
            {user.profilePictureUrl ? (
              <img
                src={user.profilePictureUrl}
                alt={user.name}
                className="w-24 h-24 rounded-full object-cover"
              />
            ) : (
              user.name?.charAt(0).toUpperCase() || 'U'
            )}
          </div>
          <h3 className="text-xl font-semibold text-secondary-900 dark:text-white mb-2">
            {user.name}
          </h3>
          <p className="text-secondary-600 dark:text-secondary-400">
            {user.role === 'admin' ? 'Administrator' : 'Employee'}
          </p>
        </div>

        {/* User details */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <label className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-1">
              Email Address
            </label>
            <p className="text-secondary-900 dark:text-white">{user.email}</p>
          </div>
          <div>
            <label className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-1">
              Department
            </label>
            <p className="text-secondary-900 dark:text-white">
              {user.department || 'Not assigned'}
            </p>
          </div>
          <div>
            <label className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-1">
              Account Status
            </label>
            <span className={cn(
              'badge',
              user.isActive ? 'badge-success' : 'badge-danger'
            )}>
              {user.isActive ? 'Active' : 'Inactive'}
            </span>
          </div>
          <div>
            <label className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-1">
              Email Verification
            </label>
            <span className={cn(
              'badge',
              user.emailVerified ? 'badge-success' : 'badge-warning'
            )}>
              {user.emailVerified ? 'Verified' : 'Unverified'}
            </span>
          </div>
          <div>
            <label className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-1">
              Last Login
            </label>
            <p className="text-secondary-900 dark:text-white">
              {user.lastLoginAt ? formatDate(user.lastLoginAt) : 'Never'}
            </p>
          </div>
          <div>
            <label className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-1">
              Member Since
            </label>
            <p className="text-secondary-900 dark:text-white">
              {formatDate(user.createdAt)}
            </p>
          </div>
        </div>

        <div className="flex justify-end pt-4">
          <Button variant="outline" onClick={onClose}>
            Close
          </Button>
        </div>
      </div>
    </Modal>
  );
};

export default Users;
